body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo{
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo img{
    height: 100px;
    width: auto;
    opacity: 1;
}
.leftmenu{
    display: flex;
    justify-content: right;
    align-items: center;
    height: 100px;
}
.leftmenu a{
    text-decoration: none;
    color: #fff;
    font-weight: 600;
    padding: 0 5px 5px 5px;
    margin: 0 20px;
}
.leftmenu a:hover{
    border-bottom: 2px solid #fff;
    
}
.rightmenu{
    display: flex;
    justify-content: left;
    align-items: center;
    height: 100px;
}
.rightmenu a{
    text-decoration: none;
    color: #fff;
    font-weight: 600;
    padding: 0 5px 5px 5px;
    margin: 0 20px;
}
.rightmenu a:hover{
    border-bottom: 2px solid #fff;
}
.nav-head{
    padding: 20px 0px 0px 0px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
}
.hamburger{
    display: none;
}
.logo-m{
    display: none;
}
.nav-head.active{
    background-color: #fff;
    position: fixed;
    padding: 10px 0px 10px 0px;
    box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.75);
    -webkit-animation-name: navscroll;
            animation-name: navscroll;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
    
}
@-webkit-keyframes navscroll{
    from {
        opacity: 0;
        margin-top: -20px;
    }
  to {
      opacity: 1;
      margin-top: 0px;
  }
}
@keyframes navscroll{
    from {
        opacity: 0;
        margin-top: -20px;
    }
  to {
      opacity: 1;
      margin-top: 0px;
  }
}
.nav-head.active .rightmenu a{
    text-decoration: none;
    color: #333;
    font-weight: 600;
    padding: 0 5px 5px 5px;
    margin: 0 20px;
}
.nav-head.active .leftmenu a{
    text-decoration: none;
    color: #333;
    font-weight: 600;
    padding: 0 5px 5px 5px;
    margin: 0 20px;
}
.nav-head.active .logo{
    display: none;
}
.nav-head.active .logo-m{
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav-head.active .logo-m img{
    height: 90px;
    width: auto;
    opacity: 1;
}

@media (max-width: 480px) {
