    }
    .spacer{
        padding: 10px 0px !important;
    }
    .card-img img {
        height: 300px;
        width: 100% !important;
        object-fit: cover;
    }
    .countdown-cont{
        padding: 50px 0px!important;
    }
    .count h1 {
        font-size: 1.8rem !important;
        color: #fff;
        margin: 0;
    }
    .count p{
        font-size: 12px;
    }

    .ach{
        padding: 10px;
    }
    .testimonial{
