    }
    .testimonial-card-img {
        background-color: #fff;
        height: 70px !important;
        width: 70px !important;
        border-radius: 35px !important;
    }
    .testimonial-card p{
        font-size: 14px !important;
    }
    .about-title h2{
        font-size: 20px !important;
        text-align: center;
    }
    .card-s-img img{
        width: 100% !important;
    }
    .left-s{
        padding: 0 !important;
    }
    .portfolio-home{
        padding: 5px !important;
    }
    .portfolio-img img{
        width: 100% !important;
    }
    .btn-project button{
        width: 100% !important;
    }
    .team-s{
        padding: 20px !important;
    }
    .img-project img{
        height: 150px;
    }
    .about-home h1{
        margin: 0 !important;
    }
    .nav-head.active .logo-m img{
        height: 60px !important;
    }
    .nav-mob{
        background: #666;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 99;
        height: 100px;
        width: 100px;
    }
    .logo a{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .nav-head.active .logo-m{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .logo-m a{
        display: flex;
    }

}

